import React from 'react'

const buttonStyle = {
  width: '100%',
  backgroundColor: '#81F7E5',
  border: 'none',
  fontWeight: 'bold',
  padding: '4px',
}

export default function Personform({
  handleSubmit,
  newName,
  setNewName,
  newNumber,
  setNewNumber,
}) {
  return (
    <form className="App-form" onSubmit={handleSubmit}>
      <div>
        <input
          className="placeholder-gray-500 placeholder-opacity-25 pl-3 my-1 border-2 rounded border-blue-400 text-gray-900"
          placeholder="Enter a Name"
          value={newName}
          onChange={(e) => setNewName(e.target.value)}
        />
      </div>
      <div>
        <input
          className="placeholder-gray-500 placeholder-opacity-25 pl-3 my-1 border-2 rounded border-blue-400 text-gray-900"
          placeholder="Enter a Number"
          value={newNumber}
          onChange={(e) => setNewNumber(e.target.value)}
        />
      </div>
      <div>
        <button style={buttonStyle} className="uppercase" type="submit">
          add to phonebook
        </button>
      </div>
    </form>
  )
}
