import { useState, useEffect } from 'react'
import './App.css'
import Filter from './components/Filter'
import Personform from './components/Personform'
import PersonsDisplay from './components/PersonsDisplay'
import personService from './services/persons'

function App() {
  const [persons, setPersons] = useState([])
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [filter, setFilter] = useState('')

  useEffect(() => {
    personService.getAll().then((allPersons) => {
      setPersons(allPersons)
    })
  }, [])
  console.log(persons)

  const handleSubmit = (e) => {
    e.preventDefault()
    const isInPhonebook = persons.map(
      (person) => person.name.toLowerCase() !== newName.toLowerCase()
    )
    const newPerson = { name: newName, number: newNumber }
    !isInPhonebook.includes(false) &&
      personService.create(newPerson).then((returnedPerson) => {
        setPersons([...persons, returnedPerson])
        setNewName('')
        setNewNumber('')
      })
  }

  const handleFilter = (e) => {
    setFilter(e.target.value)
  }

  return (
    <div className="App">
      <header className="App-header">
        <h2 className="font-black text-4xl">Phonebook</h2>
        <h3 className="text-2xl font-black pt-10">Find a name</h3>
        <Filter filter={filter} handleFilter={handleFilter} />
        <h3 className="text-2xl font-black pt-10">Enter a new contact</h3>
        <Personform
          newName={newName}
          setNewName={setNewName}
          newNumber={newNumber}
          setNewNumber={setNewNumber}
          handleSubmit={handleSubmit}
        />
        <h2 className="font-black text-3xl pt-10">Numbers</h2>
        <PersonsDisplay
          persons={persons}
          setPersons={setPersons}
          filter={filter}
        />
      </header>
    </div>
  )
}

export default App
