import personService from '../services/persons'

export default function PersonsDisplay({ persons, setPersons, filter }) {
  const handleClick = (id) => {
    setPersons(persons.filter((p) => p.id !== id))
    personService.destroy(id)
  }
  return (
    <ul>
      {persons
        .filter((person) =>
          person.name.toLowerCase().includes(filter.toLowerCase())
        )
        .map((filteredName) => (
          <div
            className="flex justify-between items-center"
            key={filteredName.id}
          >
            <h2>
              {filteredName.name} - {filteredName.number}
            </h2>
            <button
              className="bg-red-500 py-2 px-4 my-1 rounded"
              onClick={() => handleClick(filteredName.id)}
            >
              delete
            </button>
          </div>
        ))}
    </ul>
  )
}
